import {sort} from '../../src/utils'

const
    canvas = wx.getSharedCanvas(),
    ctx = canvas.getContext('2d'),
    misc = {
        row: {}
    }


wx.onMessage(async ev => {
    switch (ev.type) {
        case 'rank:create': {
            const list = await getFriendCloudStorage({keyList: ['score']}).catch(err => [])
            render(list, ev.data.zoom)
            break
        }

        case 'rank:clear': {
            ctx.clearRect(0, 0, canvas.width, canvas.height)
            break
        }

        case 'rank:ok': {
            ctx.fillStyle = '#363636'
            ctx.fillRect(0, 0, 1, 1)
            break
        }
    }
})

function getFriendCloudStorage(option) {
    return new Promise((resolve, reject) => {
        wx.getFriendCloudStorage({
            ...option,
            success: res => {
                resolve(res.data.map(item => ({
                    score: JSON.parse(item.KVDataList[0].value).score,
                    ...item
                })))
            },
            fail: reject
        })
    })
}


function render(list, zoom=1) {
    misc.zoom = zoom
    misc.width = canvas.width
    misc.row.height = 100 * misc.zoom
    misc.colors = ['#e5702f', '#e58634', '#eace50']


    // ctx.fillStyle = '#363636'
    ctx.clearRect(0, 0, canvas.width, canvas.height)

    if (!list.length) return drawTip()

    list = sort(list, (a, b) => b.score - a.score)
    list.forEach((item, i) => {
        drawRow({
            i,
            name: item.nickname,
            score: item.score,
            url: item.avatarUrl,
            amount: list.length
        })
    })
}

function drawTip() {
    ctx.fillStyle = '#363636'
    ctx.fillRect(0, 0, misc.width, misc.row.height)

    ctx.textAlign = 'center'
    ctx.textBaseline = 'middle'

    ctx.font = `${30 * misc.zoom}px sans-serif`
    ctx.fillStyle = '#fff'
    ctx.fillText('暂无数据', misc.width * .5, misc.row.height * .5)

    ctx.fillStyle = '#fff'
    ctx.fillRect(0, 0, 1, 1)
}

function drawRow({i, name, score, url, amount}) {
    const
        rank = i + 1,
        y = i * misc.row.height,
        w = 60 * misc.zoom

    ctx.textBaseline = 'middle'
    ctx.textAlign = 'center'

    /* 背景 */
    ctx.fillStyle = rank % 2 ? '#363636' : '#3c3b3d'
    ctx.fillRect(0, y, misc.width, misc.row.height)

    /* 名次 */
    ctx.fillStyle = rank < 3 ? misc.colors[rank] : '#fff'
    ctx.font = `italic bold ${30 * misc.zoom}px sans-serif`
    ctx.fillText(rank, 60 * misc.zoom, y + misc.row.height * .5)

    /* 头像 */
    loadImage(url).then(avatar => {
        ctx.drawImage(avatar, 120 * misc.zoom, y + (misc.row.height - w) * .5, w, w)
        if (rank === amount) {
            ctx.fillStyle = '#ff0000'
            ctx.fillRect(0, 0, 1, 1)
        }
    })

    /* 昵称 */
    ctx.textAlign = 'left'
    ctx.font = `${32 * misc.zoom}px sans-serif`
    ctx.fillStyle = '#fff'
    ctx.fillText(name, 220 * misc.zoom, y + misc.row.height * .5)

    /* 分数 */
    ctx.textAlign = 'right'
    ctx.font = `${34 * misc.zoom}px sans-serif`
    ctx.fillText(score, misc.width - 30 * misc.zoom, y + misc.row.height * .5)
}

function loadImage(url) {
    return new Promise(resolve => {
        const img = wx.createImage()
        img.src = url
        img.onload = () => resolve(img)
    })
}